import { useEffect, useState } from 'react';
export const useMobileVideoOverlayModel = ({ location }) => {
    const [model, setModel] = useState();
    useEffect(() => {
        if (!location) {
            return;
        }
        const requestModel = async () => {
            const command = {
                sender: 'asbplayer-mobile-overlay-to-video',
                message: {
                    command: 'request-mobile-overlay-model',
                },
                src: location.src,
            };
            const initialModel = await chrome.runtime.sendMessage(command);
            setModel(initialModel);
        };
        let timeout;
        let cancelled = false;
        const init = async () => {
            try {
                if (cancelled) {
                    return;
                }
                await requestModel();
            }
            catch (e) {
                console.log('Failed to request overlay model, retrying in 1s. Message: ' +
                    (e instanceof Error ? e.message : String(e)));
                timeout = setTimeout(() => init(), 1000);
            }
        };
        init();
        return () => {
            if (timeout !== undefined) {
                clearTimeout(timeout);
            }
            cancelled = true;
        };
    }, [location]);
    useEffect(() => {
        if (!location) {
            return;
        }
        const listener = (message, sender, sendResponse) => {
            if (message.sender !== 'asbplayer-video-to-mobile-overlay' || message.src !== location.src) {
                return;
            }
            const command = message;
            setModel(command.message.model);
        };
        chrome.runtime.onMessage.addListener(listener);
        return () => chrome.runtime.onMessage.removeListener(listener);
    }, [location]);
    return model;
};
