import { useCallback, useEffect, useState, useMemo } from 'react';
import { ControlType } from '..';
export const useLastScrollableControlType = ({ isMobile, fetchLastControlType, saveLastControlType }) => {
    const [lastControlType, setLastControlType] = useState();
    const defaultControlType = useMemo(() => (isMobile ? ControlType.timeDisplay : ControlType.subtitleOffset), [isMobile]);
    useEffect(() => {
        fetchLastControlType().then((controlType) => {
            if (controlType === undefined) {
                setLastControlType(defaultControlType);
            }
            else {
                setLastControlType(controlType);
            }
        });
    }, [defaultControlType, fetchLastControlType]);
    const wrapSaveLastControlType = useCallback((controlType) => {
        setLastControlType(controlType);
        saveLastControlType(controlType);
    }, [saveLastControlType]);
    return { lastControlType, setLastControlType: wrapSaveLastControlType };
};
export default useLastScrollableControlType;
